<template>
  <dir class="settings">
    <h3>Настройки</h3>
    <h5>Дневник</h5>
    <div class="switch">
      <label>
        Полосы между уроками
        <input type="checkbox" v-model="settings.diaryStripes" />
        <span class="lever"></span>
      </label>
    </div>
    <div class="switch">
      <label>
        Новая неделя в воскресенье
        <input type="checkbox" v-model="settings.noIncludeSunday" />
        <span class="lever"></span>
      </label>
    </div>
    <p class="range-field">
      Обратный отсчет после выбора
      <input
        min="1"
        max="5"
        step="0.25"
        type="range"
        ref="diaryCountdown"
        v-model="settings.diaryCountdown"
      />
    </p>
  </dir>
</template>

<script>
import { mapState, mapActions } from "vuex";

export default {
  data: () => ({
    instances: []
  }),
  computed: mapState({
    settings: state => state.settings.storage
  }),
  methods: mapActions(["saveSettings"]),
  watch: {
    "settings.diaryStripes"(value) {
      this.saveSettings({
        name: "diaryStripes",
        value
      });
    },
    "settings.noIncludeSunday"(value) {
      this.saveSettings({
        name: "noIncludeSunday",
        value
      });
    },
    "settings.diaryCountdown"(value) {
      this.saveSettings({
        name: "diaryCountdown",
        value: +value
      });
    }
  },
  created() {
    setTimeout(() => {
      // eslint-disable-next-line no-undef
      this.instances.push(M.Range.init(this.$refs.diaryCountdown));
    }, 0);
  },
  destroyed() {
    for (const instance of this.instances) {
      instance?.destroy?.();
    }
  }
};
</script>

<style scoped>
@import "../assets/settings.css";
</style>
